<template>
  <div>
    <!---轮播图-->
    <mt-swipe :auto="4000">
      <mt-swipe-item v-for="(item ,index) in imgList" :key="index">
        <img :src="item.img" />
      </mt-swipe-item>
    </mt-swipe>
    <!---九宫格-->
    <div class="grid">
      <my-ul>
        <my-li v-for="(module,index) in modules" :key="index">
          <router-link :to="module.route">
            <span :class="module.className"></span>
            <div>{{module.title}}</div>
          </router-link>
        </my-li>
      </my-ul>  
    </div>  
  </div>
</template>

<script>
export default {
  name: 'Home',
  data () {
    return {
      imgList:[],//轮播图数据
      modules:[{
        title:'猫咪列表',
        className:'back-news',
        route:{name:'NewsList'}
      },{
        title:'图文分享',
        className:'back-picShare',
        route:{name:'PhotoList',query:{categoryId:0} }
      },{
        title:'猫咪出售',
        className:'back-goodsShow',
        route:{name:'GoodsShow',params:{page:1} }
      }]
    }
  },
  created() {
    //发送请求
    this.$axios.get('/json/lunbo.json')
    .then(res =>{
     this.imgList = res.data.imgList;
    })
    .catch(err => console.log(err))
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
/*****轮播图 */
.mint-swipe{
  width: 100%;
  height: 200px;
}
.mint-swipe img{
  width: 100%;
  height: 100%;
}
/*****九宫格 */
.grid span {
    display: inline-block;
    margin: 10px 0;
    width: 50px;
    height: 50px;
    background-repeat: round;
}


.back-news {
    background-image: url(../../static/img/cats.png);

}
.back-picShare {
    background-image: url(../../static/img/cat.png);

}
.back-goodsShow {
    background-image: url(../../static/img/goods.png);

}



</style>



