// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import store from './store/store'
import '../static/css/globat.css' //引入全局样式

Vue.config.productionTip = false

import MintUI from 'mint-ui' // 引入mint-ui全部组件
import 'mint-ui/lib/style.css' // 引入css
Vue.use(MintUI) // 安装插件

//引入axios
import Axios from 'axios';
//使用插件，挂载到vue
Vue.prototype.$axios = Axios;


//注册全局组件
import MyUl from './components/common/MyUl';
Vue.component(MyUl.name, MyUl);
import MyLI from './components/common/MyLI';
Vue.component(MyLI.name, MyLI);
import NavBar from './components/common/NavBar';
Vue.component(NavBar.name, NavBar);
import Comment from './components/common/Comment';
Vue.component(Comment.name, Comment);
import MySwipe from './components/common/Swipe';
Vue.component(MySwipe.name, MySwipe);

//定义全局过滤器
Moment.locale('zh-cn');
import Moment from 'moment';
Vue.filter('convertTime', function(data, formatStr) {
    return Moment(data).format(formatStr);
});
// 相对时间过滤器
Vue.filter('relTime', function(time) {
    // 2015-04-16T03:50:28.000Z
    return Moment(time).fromNow();
});
// 控制字数显示的过滤器
Vue.filter('controllShow', function(str, num) {
    // 佳能（Canon） EOS 700D 单反套机

    // 如果当前字符串小于num,返回原值
    if (str.length <= num) {
        return str;
    }
    // 如果当前字符串大于num,截取0-num-1位
    if (str.length > num) {
        return str.substr(0, num - 1) + '...';
    }

});

// 定义拦截器
// 1: 请求发起前显示loading open();
Axios.interceptors.request.use(function(config) {
        // 不变配置:  可变，可以设置公共的请求头操作
        MintUI.Indicator.open({
            text: '加载中...',
            spinnerType: 'fading-circle'
        });
        // console.log(config);
        return config; // config:{ headers}
    })
    // 2: 响应回来后关闭loading close()
Axios.interceptors.response.use(function(response) {
        //reponse: { config:{ },data:{} ,headers }

        // 接收响应头或者响应体中的数据，保存起来，供请求的拦截器中使用头信息操作
        MintUI.Indicator.close();
        // console.log(response);
        return response;
    })
    //图片预览组件
import VuePreview from 'vue-preview'
// defalut install
Vue.use(VuePreview)


/* eslint-disable no-new */
new Vue({
    el: '#app',
    router,
    store,
    components: { App },
    template: '<App/>'
})


