<template>
 <div id="tmp1">
    <mt-header :title="title">
    <div @click="goback" slot="left">
        <mt-button icon="back">返回</mt-button>
    </div>
    <mt-button icon="more" slot="right"></mt-button>
    </mt-header>
 </div>
</template>

<script>
 export default {
   name:'nav-bar',
   props:['title'],//props属性在template中可以直接用
   created() {
       //在这里使用this.title

   },
   data () {
     return {

     }
   },methods: {
       goback(){
           //根据历史记录逐步返回
           this.$router.go(-1);
       }
   },
 }
</script>

<style scoped>
#tmp1 .mint-header{
    background-color: white;
    color: black;
}
 
</style>


