<template>
    <div>  
        <nav-bar title="图文详情"/>
        <div class="photo-title">
            <p>{{photoInfo.title}}</p>
            <span>发起日期：{{photoInfo.add_time | convertTime('YYYY年MM月DD日')}}</span>
            <span>{{photoInfo.click}}次浏览</span>
            <span>分类：猫科</span>
        </div>
        <my-ul>
            <!--<vue-preview :slides="imgs"></vue-preview>-->   
            <my-li v-for="(img,index) in imgs" :key="index">
               <img :src="img.src" slot="icon"/>
            </my-li> 
        </my-ul>
        <div class="photo-desc" v-for="(content,index) in photoInfo.content" :key="index">
            <img :src="content.img_url" style="width:100%;" />
            <p>{{content.zhaiyao}}</p>
        </div>
        <!-- 使用评论组件 -->
        <comment :cid="$route.params.id"/>
    </div>
</template>
<script>
    export default{
        data() {
            return {
                photoInfo:{},//图文详情
                imgs:[]//缩略图
            }
        },
        created() {
            console.log(this.$route.params.id);
            let pid = this.$route.params.id;
            // 发起请求 获取图文详情信息
            this.$axios.get(`/json/getimageInfo.json?pid=${pid}`)
            .then(res=>{
                this.photoInfo = res.data.message[pid-1];
            })
            .catch(console.log);
            //获取缩略图
            this.$axios.get(`/json/getthumimages.json`)
            .then(res=>{
                const message = res.data.message;
                this.imgs = message.filter(i => i.id == pid);
                //  给图片对象加入宽高
                /*this.imgs.forEach( img => {
                    img.w = 100;
                    img.h = 200;
                    img.msrc = img.src;
                });*/
            })
            .catch(console.log);

        },
    }
</script>
<style scoped>
li {
    list-style: none;
}

ul {
    margin: 0;
    padding: 0;
}

.photo-title {
    overflow: hidden;
}

.photo-title,
.photo-desc {
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
    padding-bottom: 5px;
    margin-bottom: 5px;
    padding-left: 5px;
}

.photo-title p {
    color: #13c2f7;
    font-size: 20px;
    font-weight: bold;
}

.photo-title span {
    margin-right: 5px;
}

.photo-desc p {
    font-size: 18px;
}

</style>



