<template>
  <div>
    <nav-bar title="图文分享" />
    <!-- 引入返回导航 -->
    <div class="photo-header">
      <ul>
        <li v-for="category in categories" :key="category.id">
          <a href="javascript:;" @click="navigateToCateById(category.id)">{{category.title}}</a>
        </li>
      </ul>
    </div>
    <div class="photo-list">
      <ul>
        <li v-for="(img,index) in imgs" :key="index">
          <router-link :to="{ name:'PhotoDetail',params:{id:img.id} }">
            <img v-if="$route.query.categoryId === 0" v-lazy="img.img_url" style="width:100%;"/>
            <img v-else :src="img.img_url" style="width:100%;"/>
            <p>
              <span>{{img.title}}</span>
              <br />
              <span>{{img.zhaiyao}}</span>
            </p>
          </router-link>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      imgs: [], // 图片数据
      categories: [] // 分类信息
    };
  },//路由更新(路由参数改变)
  beforeRouteUpdate (to, from, next) {
   // console.log(to);
   // console.log(from);
   let {categoryId} = to.query;
   if (categoryId === 0 && this.imgs.length != 0) {
     this.imgs = [];
   } 
   //发送请求更改页面数据
   this.loadImgsById(categoryId);
   next();
  },
  methods: {
    // 跳转页面
    navigateToCateById(id) {
      this.$router.push({
        name:'PhotoList',
        query:{ categoryId:id }
      });
    },
    loadImgsById(categoryId) {
      console.log(categoryId);
      // 2: 发起请求
      this.$axios
        .get("/json/getimages.json")
        .then(res => {
          // 3: 渲染数据
          const message = res.data.message;
          if (categoryId == 0) {
            this.imgs = message;
          } else {
            this.imgs = message.filter(i => i.categoryid == categoryId);
          }
        })
        .catch(console.log);
    }
  },
  created() {
    // 1: 获取分类id
    let { categoryId } = this.$route.query;
    this.loadImgsById(categoryId);
    // 获取分类信息
    this.$axios
      .get("/json/getimgcategory.json")
      .then(res => {
        this.categories = res.data.message;
        // 加入全部到数组的顶部
        this.categories.unshift({ id: 0, title: "全部" });
      })
      .catch(console.log);
  }

};
</script>
<style scoped>
.photo-header li {
  list-style: none;
  display: inline-block;
  margin-left: 10px;
  height: 30px;
}

.photo-header ul {
  /*强制不换行*/
  white-space: nowrap;
  overflow-x: auto;
  padding-left: 0px;
  margin: 5;
}

/*下面的图片*/

.photo-list li {
  list-style: none;
  position: relative;
}

.photo-list li img {
  width: 100%;
  height: 230px;
  vertical-align: top;
}

.photo-list ul {
  padding-left: 0px;
  margin: 0;
}

.photo-list p {
  position: absolute;
  bottom: 0px;
  color: white;
  background-color: rgba(0, 0, 0, 0.3);
  margin-bottom: 0px;
}

.photo-list p span:nth-child(1) {
  font-weight: bold;
  font-size: 16px;
}
.photo-list p span {
  display: -webkit-box;
  overflow: hidden;
  text-overflow: ellipsis;
  word-wrap: break-word;
  white-space: normal !important;
  -webkit-line-clamp: 6;
  -webkit-box-orient: vertical;
}

image[lazy="loading"] {
  width: 40px;
  height: 300px;
  margin: auto;
}
</style>



